import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { AppBar, Toolbar, Button, IconButton, Grid } from "@material-ui/core";
import HomeRoundedIcon from "@material-ui/icons/HomeRounded";
import AccountCircleRoundedIcon from "@material-ui/icons/AccountCircleRounded";
import WorkRoundedIcon from "@material-ui/icons/WorkRounded";
import BuildRoundedIcon from "@material-ui/icons/BuildRounded";
import Name from "../images/name.png";
import * as ColorCodes from "../ColorCodes";

const useStyles = (theme) => ({
  appBar: {
    backgroundColor: "rgba(255, 255, 255, 0.8)",
    boxShadow: "none",
  },
  title: {
    backgroundColor: "none",
    padding: "0px"
  },
  titleButton: {
    marginLeft: "35px",
    marginTop: "15px",
    fontSize: "20px",
    color: ColorCodes.DARK_GREY_TEXT,
    textTransform: "none",
    "&:hover": {
      transform: "scale(1.1)"
    }
  },
  icon: {
    marginRight: "15px",
  }
});

class Navigation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  goToHomePage = () => {
    const anchor = document.querySelector("#home-page");
    anchor.scrollIntoView({ behavior: "smooth", block: "start" });
  }

  goToAboutMe = () => {
    const anchor = document.querySelector("#about-me");
    anchor.scrollIntoView({ behavior: "smooth", block: "start" });
  }

  goToSkills = () => {
    const anchor = document.querySelector("#skills")
    anchor.scrollIntoView({ alignToTop: true, behavior: "smooth", block: "start" })
  }

  goToExperiences = () => {
    const anchor = document.querySelector("#experiences")
    anchor.scrollIntoView({ alignToTop: true, behavior: "smooth", block: "start" })
  }

  render = () => {
    const { classes } = this.props;

    return (
      <AppBar position="fixed" className={classes.appBar}>
        {
          this.props.mobile ? 
            <Toolbar className={classes.title} style={{ spacing: "center" }}>
              <Grid container spacing={0}>
                <Grid item xs={3}>
                  <IconButton className={classes.titleButton} onClick={this.goToHomePage}>
                    <HomeRoundedIcon />
                  </IconButton>
                </Grid>
                <Grid item xs={3}>
                  <IconButton className={classes.titleButton} onClick={this.goToAboutMe}>
                    <AccountCircleRoundedIcon />
                  </IconButton>
                </Grid>
                <Grid item xs={3}>
                  <IconButton className={classes.titleButton} onClick={this.goToSkills}>
                    <BuildRoundedIcon />
                  </IconButton>
                </Grid>
                <Grid item xs={3}>
                  <IconButton className={classes.titleButton} onClick={this.goToExperiences}>
                    <WorkRoundedIcon />
                  </IconButton>
                </Grid>
              </Grid>
            </Toolbar>
            :
            <Toolbar className={classes.title}>
              <Grid container spacing={0}>
                <Grid item xs={3} style={{ textAlign: "left" }}>
                  <Button className={classes.homeButton} onClick={this.goToHomePage}>
                    <img src={Name} alt="Victoria Law" style={{ height: "65px" }} />
                  </Button>
                </Grid>
                <Grid item xs={9} style={{ textAlign: "right" }}>
                  <Button 
                    className={classes.titleButton}
                    onClick={event => window.open("https://drive.google.com/file/d/1id4mbLXODUvrWLKtsyObm7Uo8Fus8ZFH/view?usp=sharing")}
                  >
                    Resume
                  </Button> 
                  <Button className={classes.titleButton} onClick={this.goToAboutMe}>
                    About Me
                  </Button>
                  <Button className={classes.titleButton} onClick={this.goToSkills}>
                    Skills
                  </Button>
                  <Button className={classes.titleButton} onClick={this.goToExperiences}>
                    Experiences
                  </Button>
                </Grid>
              </Grid>
            </Toolbar>
        }
        
      </AppBar>
    );
  };
}

export default withStyles(useStyles)(Navigation);
